.landing {
    position: relative;
    background: url('../../assets/showcase.jpeg') no-repeat;
    background-size: cover;
    background-position: center;
    height: 100vh;
    margin-top: -24px;
    margin-bottom: -50px;
  }
  
  .landing-inner {
    padding-top: 80px;
  }
  
  .dark-overlay {
    background-color: rgba(0, 0, 0, 0.082);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }